<template>
	<div>
		<div class="container">
			<div class="row">
				<div class="col-md">
					<AppItemList title="Prefixos" v-bind:items="prefixes" v-on:addItem="addPrefix" v-on:deleteItem="deletePrefix"/>
				</div>
				<div class="col-md">
					<AppItemList title="Sufixos" v-bind:items="sufixes" v-on:addItem="addSufix" v-on:deleteItem="deleteSufix"/>
				</div>
			</div>
			<h5>Dom√≠nios <span class="badge bg-info">{{ domains.length }}</span></h5>
			<div class="card">
				<div class="card-body">
					<ul class="list-group">
						<li class="list-group-item" v-for="domain in domains" v-bind:key="domain.name">
							<div class="row">
								<div class="col-md">
									{{ domain.name }}
								</div>
								<div class="col-md text-end">
									<a v-bind:href="domain.link" target="blank" class="btn btn-info">
										<i class="fa fa-cart-shopping"></i>
									</a>
								</div>
							</div>
						</li>
					</ul>
				</div>
			</div>
			<br>
		</div>
	</div>
</template>

<script>

import AppItemList from "./AppItemList";

export default {
	name: "HelloWorld",
	components: {
		AppItemList
	},
	props: ["items", "title"],

	data: function () {
		return {
			prefixes: ["Air", "Jet", "Flight"],
			sufixes: ["Hub", "Station", "Mart"],
		};
	},
	methods: {
		addPrefix(prefix) {
			this.prefixes.push(prefix);
		},
		deletePrefix(prefix) {
			this.prefixes.splice(this.prefixes.indexOf(prefix), 1);
		},
		deleteSufix(sufix) {
			this.sufixes.splice(this.sufixes.indexOf(sufix), 1);
		},
		addSufix(sufix) {
			this.sufixes.push(sufix);
		}
	},
	computed: {
		domains() {
			const domains = [];
			for(const prefix of this.prefixes) {
				for(const sufix of this.sufixes) {
					const name = prefix + sufix;	
					const url = name.toLocaleLowerCase();
					const link = `https://cart.hostgator.com.br/?pid=d&sld=${url}&tld=.com.br&domainCycle=2`;
					domains.push({
						name,
						link
					});
				}
			}

			return domains;
		}
	}
};
</script>

